export default {
  name: 'header',
  data () {
    return {
      index: ''
    };
  },
  methods: {
    toSearch () {
      this.$router.push('/search');
    },
    showAsideMenu (flag) {
      this.$store.commit('showAsideMenu', flag);
    }
  }
};



